import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { CameraResultType, CameraSource, Plugins } from '@capacitor/core';
import { AlertController, Platform, IonItemSliding } from '@ionic/angular';
import { Category, Restaurant } from 'src/Table/table';
import { CategoryService } from '../Service/category.service';
import { RestaurantService } from '../Service/restaurant.service';
@Component({
  selector: 'app-category',
  templateUrl: './category.page.html',
  styleUrls: ['./category.page.scss'],
})
export class CategoryPage implements OnInit {
  @ViewChild('filePicker', { static: false }) filePickerRef: ElementRef<HTMLInputElement>;
  regform = this.fb.group({})
  categoryId: string;
  editMode: boolean
  listOfCategory: Category[];
  usePicker: boolean;
  base64textString: string;
  listOfRestaurant: Restaurant[];
  defalutRestaurantId: string;
  listOfCatagoryFilter: Category[];
  constructor(private fb: FormBuilder, 
    private categoryService: CategoryService,
    private alertController: AlertController,
    private restaurantService:RestaurantService,
     private platform:Platform) { }

  ngOnInit() {
    this.regform = this.fb.group({
      categoryName:  ["", Validators.required],
      description: [""],
      image:[""],
      restaurantId:["",Validators.required]
      });
    if ((this.platform.is('mobile') && !this.platform.is('hybrid')) ||
    this.platform.is('desktop')
  ) {
    this.usePicker = true;
  }
    this.getCategory();
    this.getRestaurant();
  }
  getCategory(){
    this.categoryService.getAllCategory().subscribe(res=>{
      this.listOfCategory = res;
    })
  }
  getRestaurant(){
    this.restaurantService.getAllRestaurant().subscribe(res=>{
      this.listOfRestaurant = res;
      this.defalutRestaurantId = res[0].id
    })
  }
  selectRestaurant(event){ 
    if(event!= undefined){
      this.listOfCatagoryFilter = this.listOfCategory.filter(c=>c.restaurantId==event);
    }
  }
  SaveCategory() {
    if (this.base64textString !== undefined) {
      this.regform.get('image').setValue(this.base64textString);
    }
    if (this.regform.valid) {
      if (!this.categoryId) {
        this.categoryService.create(this.regform.value);
      }
      else {
        this.categoryService.updateCategory(this.regform.value, this.categoryId);
      }
      this.regform.reset();
      this.base64textString=''
      this.presentAlert();
      this.categoryId = null;
    }
    else {
      this.ErrorAlert();
    }
  }
  edit(category:Category,sliding:IonItemSliding){
    this.editMode=true;
    this.categoryId = category.id;
    this.regform.get('categoryName').setValue(category.categoryName);
    this.regform.get('description').setValue(category.description);
    this.regform.get('restaurantId').setValue(category.restaurantId);
    this.base64textString = category.image;
    sliding.close();
  }
  delete(category:Category , sliding:IonItemSliding){
   this.presentAlertConfirm(category);
   sliding.close();
  }
  async presentAlertConfirm(category: Category) {
    const alert = await this.alertController.create({
      header: 'Confirm!',
      message: '<strong>Are you sure you want delete?</strong>',
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          cssClass: 'secondary',
          handler: (blah) => {
            console.log('Confirm Cancel: blah');
          }
        }, {
          text: 'OK',
          handler: () => {
            this.categoryService.removeCategory(category.id);
            this.regform.reset();
            this.categoryId = null;
          }
        }
      ]
    });

    await alert.present();
  }
  async ErrorAlert() {
    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Error',
      // subHeader: 'Subtitle',
      message: 'Please Enter All field.',
      buttons: ['OK']
    });

    await alert.present();
  }
  async presentAlert() {
    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Category',
      // subHeader: 'Subtitle',
      message: 'Category Saved successfully.',
      buttons: ['OK']
    });

    await alert.present();
  }
  onPickImage() {
    if (this.usePicker == true) {
      // if (!Capacitor.isPluginAvailable('Camera')) {
      this.filePickerRef.nativeElement.click();
      return;
    }
    Plugins.Camera.getPhoto({
      quality: 50,
      source: CameraSource.Prompt,
      correctOrientation: true,
      height: 100,
      width: 100,
      resultType: CameraResultType.Base64
    })
      .then(image => {
        this.base64textString = 'data:image/png;base64,' + image.base64String;
      })
      .catch(error => {
        console.log(error);
        if (this.usePicker) {
          this.filePickerRef.nativeElement.click();
        }
        return false;
      });
  }
  onFileChosen(event: Event) {
    const pickedFile = (event.target as HTMLInputElement).files[0];
    if (!pickedFile) {
      return;
    }
    const fr = new FileReader();
    fr.onload = () => {
      const dataUrl = fr.result.toString();
      this.base64textString = dataUrl;
    };
    fr.readAsDataURL(pickedFile);
  }
}
